"use client"

import { Card, CardContent } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { ProgressBar } from './progress-bar'

interface RewardCardProps {
  id: string
  title: string
  description: string
  pointsRequired: number
  currentPoints: number
  category: 'digital' | 'physical' | 'experience'
  available: boolean
  claimed?: boolean
  imageColor?: string
  onClaim?: (id: string) => void
}

export function RewardCard({
  id,
  title,
  description,
  pointsRequired,
  currentPoints,
  category,
  available,
  claimed = false,
  imageColor = 'bg-blue-500',
  onClaim
}: RewardCardProps) {
  const canClaim = currentPoints >= pointsRequired && available && !claimed
  const progress = Math.min((currentPoints / pointsRequired) * 100, 100)

  const getCategoryBadge = () => {
    const categories = {
      digital: { label: 'Digital', color: 'bg-blue-100 text-blue-800' },
      physical: { label: 'F√≠sico', color: 'bg-green-100 text-green-800' },
      experience: { label: 'Experi√™ncia', color: 'bg-purple-100 text-purple-800' }
    }

    const config = categories[category]
    return (
      <Badge className={`${config.color} border-0 text-xs`}>
        {config.label}
      </Badge>
    )
  }

  const getStatusBadge = () => {
    if (claimed) {
      return (
        <Badge className="bg-gray-100 text-gray-600 border-0 text-xs">
          Resgatado
        </Badge>
      )
    }
    if (canClaim) {
      return (
        <Badge className="bg-green-100 text-green-800 border-0 text-xs animate-pulse">
          Dispon√≠vel!
        </Badge>
      )
    }
    return null
  }

  return (
    <Card className={`
      shadow-md hover:shadow-lg transition-all duration-300 border-0
      ${canClaim ? 'ring-2 ring-green-500 ring-opacity-50' : ''}
      ${claimed ? 'opacity-75' : ''}
    `}>
      <CardContent className="p-6">
        <div className="flex items-start justify-between mb-4">
          <div className="flex space-x-2">
            {getCategoryBadge()}
            {getStatusBadge()}
          </div>
          
          {/* √çcone da recompensa */}
          <div className={`w-12 h-12 ${imageColor} rounded-xl flex items-center justify-center`}>
            <div className="w-6 h-6 bg-white rounded-full opacity-90"></div>
          </div>
        </div>

        <div className="mb-4">
          <h3 className="font-poppins font-bold text-lg text-gray-900 mb-2">
            {title}
          </h3>
          <p className="text-sm text-gray-600 leading-relaxed">
            {description}
          </p>
        </div>

        <div className="mb-4">
          <div className="flex items-center justify-between mb-2">
            <span className="text-sm font-medium text-gray-700">
              Progresso
            </span>
            <span className="text-sm font-bold text-blue-600">
              {pointsRequired} pontos
            </span>
          </div>
          
          <ProgressBar
            current={currentPoints}
            target={pointsRequired}
            animated={true}
            showNumbers={false}
          />
        </div>

        <div className="flex items-center justify-between">
          <div className="text-sm text-gray-600">
            {claimed ? (
              <span className="text-gray-500">J√° resgatado</span>
            ) : canClaim ? (
              <span className="text-green-600 font-semibold">Pronto para resgatar!</span>
            ) : (
              <span>
                Faltam <span className="font-semibold text-blue-600">
                  {pointsRequired - currentPoints}
                </span> pontos
              </span>
            )}
          </div>

          <Button
            onClick={() => onClaim?.(id)}
            disabled={!canClaim}
            size="sm"
            className={`
              rounded-lg font-semibold transition-all duration-300
              ${canClaim 
                ? 'bg-green-600 hover:bg-green-700 text-white shadow-md hover:shadow-lg transform hover:scale-105' 
                : 'bg-gray-200 text-gray-500 cursor-not-allowed'
              }
            `}
          >
            {claimed ? 'Resgatado' : canClaim ? 'Resgatar' : 'Bloqueado'}
          </Button>
        </div>
      </CardContent>
    </Card>
  )
}
